<template>
  <div id="cb">
    <el-row>
      <el-col :xs="24" :sm="24" :md="20" :lg="20" :xl="20">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>
      <el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4" class="text-right">
        <el-button type="primary" @click="showDrawerNewBtn()">Novo Botão</el-button>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-row>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="label" label="Nome do Botão"></el-table-column>
            <el-table-column prop="action" label="Workflow"></el-table-column>
            <el-table-column prop="view" label="Objeto"></el-table-column>
            <el-table-column>
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="openEdit(scope.row.cb_id)"
                  circle
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="deleteButton(scope.row.cb_id)"
                  circle
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-col>
    </el-row>

    <el-drawer title="Novo Campo" :visible.sync="drawer.new.visible" direction="rtl">
      <div class="drawer-content">
        <el-form ref="form" :model="drawer.new.form" label-width="120px">
          <el-form-item label="Label">
            <el-input v-model="drawer.new.form.label"></el-input>
          </el-form-item>
          <el-form-item label="Workflow">
            <el-input v-model="drawer.new.form.action"></el-input>
          </el-form-item>
          <el-form-item label="Layout">
            <el-select v-model="drawer.new.form.view">
              <el-option
                v-for="item in drawer.new.form.viewOptions"
                :key="item"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" @click="newButton()">Novo Botão</el-button>
        </el-form>
      </div>
    </el-drawer>

    <el-drawer title="Editar Campo" :visible.sync="drawer.update.visible" direction="rtl">
      <div class="drawer-content">
        <el-form ref="form" :model="drawer.update.form" label-width="120px">
          <el-form-item label="Label">
            <el-input v-model="drawer.update.form.label"></el-input>
          </el-form-item>
          <el-form-item label="Workflow">
            <el-input v-model="drawer.update.form.action"></el-input>
          </el-form-item>
          <el-form-item label="Layout">
            <el-select v-model="drawer.update.form.view">
              <el-option
                v-for="item in drawer.update.form.viewOptions"
                :key="item"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" @click="saveButton(drawer.update.form.id)">Salvar</el-button>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "SettingsCustomButtons",
  data() {
    return {
      drawer: {
        new: {
          visible: false,
          form: {
            label: "",
            action: "",
            view: "",
            viewOptions: []
          }
        },
        update: {
          visible: false,
          form: {
            label: "",
            action: "",
            view: "",
            id: "",
            viewOptions: []
          },
          oldData: {
            label: "",
            action: "",
            view: ""
          }
        }
      },
      pageTitle: "Botôes Personalizados",
      tableData: []
    };
  },
  head: {
    title: {
      inner: "Configurações | Botões Personalizados",
      complement: "GuiaSales"
    }
  },
  methods: {
    getModulesForSelectObject() {
      this.$http.get("/modules").then(res => {
        if (res.status == 200) {
          var arr = [];
          var d = res.data;
          d.forEach(e => {
            var element = {
              label: e.module_label,
              value: e.module_table_name
            };
            arr.push(element);
          });
          this.drawer.new.form.viewOptions = arr; 
          this.drawer.update.form.viewOptions = arr; 
        }
      });
    },
    openEdit(id) {
      this.drawer.update.visible = true;
      this.getFieldData(id);
    },
    getFieldData(id) {
      this.$http.get("/buttons/" + id).then(res => {
        this.drawer.update.form.label = res.data[0].label;
        this.drawer.update.form.action = res.data[0].action_id;
        this.drawer.update.form.view = res.data[0].view;
        this.drawer.update.form.id = res.data[0].cb_id;

        this.drawer.update.oldData.label = res.data[0].label;
        this.drawer.update.oldData.action = res.data[0].action_id;
        this.drawer.update.oldData.view = res.data[0].view;
      });
    },
    saveButton(id) {
      if (
        this.drawer.update.oldData.label === this.drawer.update.form.label &&
        this.drawer.update.oldData.action === this.drawer.update.form.action &&
        this.drawer.update.oldData.view === this.drawer.update.form.view
      ) {
        this.$message({
          message: "Campo editado com sucesso",
          type: "success"
        });

        this.drawer.update.visible = false;
        return false;
      }

      var data = {
        label: this.drawer.update.form.label,
        action: this.drawer.update.form.action,
        view: this.drawer.update.form.view,
        id: this.drawer.update.form.id
      };
      this.$http
        .put("/buttons/" + id, data)
        .then(res => {
          this.drawer.update.visible = false;

          if (res.status == "200") {
            this.$message({
              message: "Campo editado com sucesso",
              type: "success"
            });
            this.listButtons();
          }
        })
        .catch(err => {
          this.message.error("Houve um erro" + err.response.data);
        });
    },
    deleteButton(id) {
      this.$http
        .delete("/buttons/" + id)
        .then(res => {
          if (res.status == "200") {
            this.$message({
              message: "Campo apagado com sucesso",
              type: "success"
            });
            this.listButtons();
          }
        })
        .catch(err => {
          this.message.error("Houve um erro" + err.response.data);
        });
    },
    showDrawerNewBtn() {
      this.drawer.new.visible = true;
    },
    clearAllFields() {
      this.drawer.new.form.label = "";
      this.drawer.new.form.action = "";
      this.drawer.new.form.view = "";
    },
    validateFields() {
      if (
        this.drawer.new.form.label == "" ||
        this.drawer.new.form.action == "" ||
        this.drawer.new.form.view == ""
      ) {
        this.$message.error("Você deve preencher todos os campos");
        return false;
      } else {
        return true;
      }
    },
    newButton() {
      var data = {
        label: this.drawer.new.form.label,
        action: this.drawer.new.form.action,
        view: this.drawer.new.form.view
      };
      this.$http.post("/buttons", data).then(res => {
        if (res.status == "201") {
          this.clearAllFields();
          this.drawer.new.visible = false;
          this.$message({
            message: "Campo criado com sucesso",
            type: "success"
          });
          this.listButtons();
        }
      });
    },
    listButtons() {
      this.$http.get("/buttons").then(res => {
        if (res.status == "200") {
          this.tableData = res.data;
        }
      });
    }
  },
  mounted() {
    this.listButtons();
    this.getModulesForSelectObject();
  }
};
</script>

<style lang="scss">
.drawer-content {
  padding: 0px 20px;
}

.content {
  min-height: 40vh;
  text-align: center;
}

#cb {
  #listData {
    background-color: white;
  }
}
</style>